import React, { useState, useEffect } from 'react';

import Blog from './components/Blog';
import Footer from './components/Footer';
import Notification from './components/Notification';

import blogService from './services/blogs';
import loginService from './services/login';

const App = () => {
  const [blogs, setBlogs] = useState([]);
  const [showAll, setShowAll] = useState(true);

  // const [newBlog, setNewBlog] = useState({});
  const [newTitle, setNewTitle] = useState('title...');
  const [newAuthor, setNewAuthor] = useState('author...');
  const [newUrl, setNewUrl] = useState('url...');

  const [notificationMessage, setNotificationMessage] = useState('');
  const [notificationType, setNotificationType] = useState('');
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [user, setUser] = useState(null);

  useEffect(() => {
    blogService.getAll().then((blogs) => setBlogs(blogs));
  }, []);

  useEffect(() => {
    const loggedUserJSON = window.localStorage.getItem('loggedBlogAppUser');
    console.log(loggedUserJSON);
    if (loggedUserJSON) {
      console.log('inside loggedUserJSON');

      const user = JSON.parse(loggedUserJSON);
      setUser(user);
      blogService.setToken(user.token);
    }
  }, []);

  debugger;

  const addBlog = async (event) => {
    event.preventDefault();
    console.log('button clicked', event.target);

    const blogObject = {
      title: newTitle,
      author: newAuthor,
      url: newUrl,
    };

    try {
      const returnedBlog = await blogService.create(blogObject);

      setBlogs(blogs.concat(returnedBlog));
      setNewTitle('');
      setNewAuthor('');
      setNewUrl('');

      setNotificationMessage(`Blog added with success`);
      setNotificationType('success');

      setTimeout(() => {
        setNotificationMessage('');
        setNotificationType('');
      }, 5000);
    } catch (exception) {
      setNotificationMessage(
        `Blog addition failed with exception: ${exception}`
      );
      setNotificationType('error');

      setTimeout(() => {
        setNotificationMessage('');
        setNotificationType('');
      }, 5000);
    }
  };

  const handleLogin = async (event) => {
    event.preventDefault();
    console.log('logging in with', username, password);

    try {
      const user = await loginService.login({
        username,
        password,
      });
      console.log('lotoken saved ', username, password);

      window.localStorage.setItem('loggedBlogAppUser', JSON.stringify(user));

      blogService.setToken(user.token);
      console.log('lotoken saved ', user.token);
      setUser(user);
      setUsername('');
      setPassword('');

      setNotificationMessage(`Welcome in you dear ${user.name}`);
      setNotificationType('success');

      setTimeout(() => {
        setNotificationMessage('');
        setNotificationType('');
      }, 5000);
    } catch (exception) {
      console.log('exception: ', exception);

      setNotificationMessage(`Login error: ${exception}`);
      setNotificationType('error');
      setTimeout(() => {
        setNotificationMessage('');
        setNotificationType('');
      }, 5000);
    }
  };

  const handleBlogAuthorChange = (event) => {
    console.log(event.target.value);

    setNewAuthor(event.target.value);
  };

  const handleBlogTitleChange = (event) => {
    console.log(event.target.value);

    setNewTitle(event.target.value);
  };

  const handleBlogUrlChange = (event) => {
    console.log(event.target.value);

    setNewUrl(event.target.value);
  };

  console.log('before login form');
  const loginForm = () => (
    <form onSubmit={handleLogin}>
      <div>
        username
        <input
          type="text"
          value={username}
          name="Username"
          onChange={({ target }) => setUsername(target.value)}
        />
      </div>
      <div>
        password
        <input
          type="password"
          value={password}
          name="Password"
          onChange={({ target }) => setPassword(target.value)}
        />
      </div>
      <button type="submit">login</button>
    </form>
  );

  console.log('before login form');

  const blogForm = () => (
    <form onSubmit={addBlog}>
      <input value={newAuthor} onChange={handleBlogAuthorChange} />
      <input value={newTitle} onChange={handleBlogTitleChange} />
      <input value={newUrl} onChange={handleBlogUrlChange} />
      <button type="submit">save</button>
    </form>
  );

  const logout = () => {
    console.log('logging out with', username);

    window.localStorage.removeItem('loggedBlogAppUser');
    // window.localStorage.clear()

    setNotificationMessage(`Bye, ${user.name}`);
    setNotificationType('success');

    setTimeout(() => {
      setNotificationMessage('');
      setNotificationType('');
    }, 5000);

    blogService.setToken(null);
    setUser(null);
    loginForm();
  }

  return (
    <div>
      <h1>Blogs</h1>
      <Notification message={notificationMessage} type={notificationType} />

      {user === null ? (
        loginForm()
      ) : (
        <div>
          <p>
            {user.name} logged in
            <button onClick={() => logout()}> logout </button>
          </p>
          {blogForm()}

          <ul>
            {blogs.map(
              (blog, i) => (console.log(blog), (<Blog key={i} blog={blog} />))
            )}
          </ul>
        </div>
      )}

      <Footer />
    </div>
  );
};

export default App;
